* {
  box-sizing: border-box;

  &::before,
  &::after {
    box-sizing: border-box;
  }

  padding: 0;
  margin: 0;
}


ul {
  list-style: none;
}

.wrapper {
  max-width: 1200px;
  width: 100%;
  padding: 20px;
  margin: auto;
}

.box {
  display: flex;
  justify-content: space-between;
  gap: 47px;
}